import React from 'react'
import {Avatar , Typography , Grid , Box , makeStyles} from '@material-ui/core'
import avatar from '../assets/images/avatar.jpg'
import Typed from 'react-typed'
const Skills = [
  "Web Developer",
  "C++ Programmer",
  "React Developer", 
  "JavaScript Developer", 
  "AI Enthusiast",
  "Linux Enthusiast",
]
const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    flex : 1,
    textAlign : "center",
    justifyContent: 'center',
    zIndex : 1,
  },
  avatar : {
    margin: theme.spacing(1),
    width: theme.spacing(15),
    height: theme.spacing(15)
  },
  title : {
    color : 'tomato',
    marginBottom: '3rem'
  },
  subtitle : { 
    color : 'tan',
    marginBottom : '3rem'
  },
}))
const Header = () => {
  const classes = useStyles()
  return (
    <Box className = {classes.root}>
      <Grid container justify = 'center'>
        <Avatar className = {classes.avatar} src = {avatar} alt = 'Pooya Dehghan' />
      </Grid>
      <Typography variant = 'h4' className = {classes.title}>
        <Typed strings={["Pooya Dehghan"]} typeSpeed={40}/>
      </Typography>
      <Typography variant = 'h5' className = {classes.subtitle} >
        <Typed strings={Skills} 
        typeSpeed={40}
        backSpeed = {60}
        backDelay={3}
        loop
        />
      </Typography>
    </Box>
  )
}

export default Header
