import React,{useState} from 'react'
import Drawer from '@material-ui/core/Drawer'
import {Link} from 'react-router-dom'
import {
  AppBar,
  Toolbar,
  ListItem,
  IconButton,
  ListItemIcon,
  ListItemText,
  Avatar,
  Divider,
  List,
  Typography,
  Box,
  makeStyles
} from '@material-ui/core'
import {
  ArrowBack,
  AssignmentInd,
  Home,
  Apps,
  ContactMail
} from '@material-ui/icons'
import avatar from '../assets/images/avatar.jpg'
// css styles using material ui makeStyle

const makeStyle = makeStyles(theme => ({
  root: {
    display: 'flex',
    zIndex : 10
  },
  menuSliderContainer : {
    width:250,
    background : '#511',
    height: '100%',
  },
  avatar: {
    display: 'block',
    margin: '0.5rem auto',
    width : theme.spacing(13),
    height: theme.spacing(13),
  },
  listItem : {
    color : 'tan',
  }
})
)

// items which we are displaying in drawer component

const Items = [
  {
    listIcon : <Home/>,
    listText : 'Home',
    linkPath : '/'
  },
  {
    listIcon : <AssignmentInd/>,
    listText : 'Resume',
    linkPath : '/resume'
  },
  {
    listIcon : <Apps/>,
    listText : 'portfolio'
  },
  {
    listIcon : <ContactMail/>,
    listText : 'Contacts '
  },
]

//main function of this component 
const Navbar = () => {
  const classes = makeStyle()
  const [open , setOpen ] = useState(false)
  const Slide = (slider) => {
    return (
    <Box className = {classes.menuSliderContainer} component = 'div'>
      <Avatar src={avatar} alt = "Pooya Dehgan" className = {classes.avatar} />
      <Divider />
      <List>
        {Items.map((lsItem , key) => {
        return (
          <ListItem button key = {key} component = {Link} to = {lsItem.linkPath} >
            <ListItemIcon className = {classes.listItem}>
              {lsItem.listIcon}
            </ListItemIcon>
            <ListItemText className = {classes.listItem} primary = {lsItem.listText} />
          </ListItem>
          )
        })}
      </List>
  </Box>
    )
  }
  return (
  <>
    <Box component='nav' className = {classes.root}>
      <AppBar position = 'static' style = {{ background : '#222'}}>
        <Toolbar>
          <IconButton onClick = { () => setOpen(true)}>
          <ArrowBack  style = {{ color : 'tomato'}}/>
          </IconButton>
          <Typography variant = 'h5' style = {{color: 'tan'}}>
            Portfolio
          </Typography>
        </Toolbar>
        <Drawer anchor = {'right'} open = {open} onClose = {() => setOpen(false)}>
          {Slide()}
        </Drawer>
      </AppBar>
    </Box>
  </>
  )
}

export default Navbar
