import React from 'react';
import {makeStyles} from '@material-ui/core/styles'
import {Typography, Box} from '@material-ui/core'
import Navbar from './Navbar'
const useStyles = makeStyles(theme => ({
  mainContainer : {
    backgroundColor: "#233",
  },
  timeLine : {
    position:'relative',
    padding: '1rem',
    margin:'0 auto',
    '&:before' : {
      content : "''",
      height : '100%',
      position: 'absolute',
      right : '40px',
      top:0,
      border : '1px solid tan',
    },
    '&:after' : {
      content : "''",
      display : 'table',
      clear : 'both'
    },
    [theme.breakpoints.up('md')]:{
      padding : '2rem',
      "&:before" : {
        left : "calc(50% - 1px)",
        right : "auto"
      }
    }
  },
  timeLineitem : {
    padding : "1rem",
    borderBottom : "2px solid tan",
    position : 'relative',
    margin : '1rem  3rem 1rem 1rem',
    clear : "both",
    "&:after": {
      position : 'absolute',
      contetn : "''",
    },
    "&:before" : {
      content : '"',
      position :'absolute',
      right: '-0.625rem',
      top : 'calc(50% - 5xp)',
      borderStyle : 'solid',
      borderColor : "tomato tomato transparent transparent",
      borderWidth : '0.625rem',
      transform : 'rotate(45deg)',
    },
    [theme.breakpoints.up('md')]:{
      width : '44%',
      margin : '1rem',
      "&:nth-of-type(2n)" : {
        float : 'right',
        margin : '1rem',
        borderColor : 'tan'
      },
      '&:nth-of-type(2n):before':{
        right : 'auto',
        left : "-0.625rem",
        borderColor : "transparent transparent tomato tomato"
      }
    }
  },
  timeLineYear : {
    textAlign : 'center',
    maxWidth : '9.375rem',
    margin : '0 3rem 0 auto',
    fontSize : '1.8rem',
    background : 'tomato',
    color : 'white',
    lineHeight : 1,
    padding : "0.5rem 0 1rem",
    "&:before" : {
      display : "none"
    },
    [theme.breakpoints.up('md')]:{
      textAlign : 'center',
      margin :'0 auto',
      "&:nth-of-type(2n)": { 
        float : "none",
        margin  : " 0 auto"
      },
      "&:nth-of-type(2n):before": {
        display : 'none'
      }
    },
    heading : {
      textTransform : "uppercase",
      color : 'tomato',
    },

  }
}))
const Resume = () => {
  const classes = useStyles()
  return (
    <>
      <Navbar />
      <Box component = "header" className = {classes.mainContainer}>
        <Typography variant = "h4" align = "center" className = {classes.heading} >
          working experience
        </Typography>
        <Box component = "div" className = {classes.timeLine }>
          <Typography variant = "h2" className = {`${classes.timeLineYear} ${classes.timeLineitem}`}>2013</Typography>
        </Box>
        <Box>
          <Typography variant = "h5" align = "center">
            Web designer
          </Typography>
          <Typography variant = "body1" align = "center">
            Web designer
          </Typography>
          <Typography variant = "subtitle1" align = "center">
            Adipisicing velit voluptate ipsum do nisi reprehenderit Lorem ea sunt
            Deserunt in aute magna laborum proident do fugiat in cillum.
          </Typography>
        </Box>
      </Box>
    </>
  )
}

export default Resume